import React, { Component } from 'react'
import styled from 'styled-components'

import moment from 'moment'

const DayView = styled.div`
  position: relative;
  display: grid;
  grid-row: 1/3;
  grid-column: 2/3;
  width: 100%;
  height: 100%;
  background-color: transparent;
  /* border: 2px solid rgba(220, 220, 220, 0.4); */
  box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.05);
  border: 5px solid white;
  border-radius: 25px;
  z-index: -1;

  .parent {
    display: flex;
    flex-flow: column;
    position: absolute;
    right: 5px;
    align-items: flex-end;
    text-align: center;
    padding-right: 10px;
    height: 80px;
  }
  .date {
    display: flex;
    text-transform: uppercase;
    margin: 0;
    color: rgba(100, 120, 220, 1);
    font-size: 20px;
  }
  .sideDate {
    display: flex;

    margin: 0;
    font-family: 'Montserrat', sans-serif;
    font-size: 30px;
    line-height: 30px;
    padding-top: 10px;
  }
`
const DayGrid = styled.div`
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 20px;
  grid-gap: 0.5px;
  width: 100%;
  border: none;
  margin-top: 20px;
  height: 95%;
  overflow-y: scroll;
  overscroll-behavior: contain;
  .timeCell {
    color: grey;
    border: 1px solid grey;
  }
`
const DUMMY_DATA = [
  {
    time: 6,
    am: true,
  },
  {
    time: 7,
    am: true,
  },

  {
    time: 8,
    am: true,
  },

  {
    time: 9,
    am: true,
  },

  {
    time: 10,
    am: true,
  },
  {
    time: 11,
    am: true,
  },

  {
    time: 12,
    am: false,
    pm: true,
  },

  {
    time: 1,
    am: false,
    pm: true,
  },
]

const SingleDay = props => (
  <DayView>
    <div className="parent">
      <div className="sideDate">
        {moment(props.date.toString()).format('MMMM D, YYYY')}
      </div>
      <div className="date">{moment(props.date.toString()).format('dddd')}</div>
    </div>
    <DayGrid>
      {DUMMY_DATA.map((timeblock, index) => {
        return (
          <div className="timeCell" key={index}>
            {timeblock.time}
          </div>
        )
      })}
    </DayGrid>
  </DayView>
)

export default SingleDay
